/**
 * Validates required environment variables at startup and exports a typed env object.
 * Run on config load so the app fails fast with a clear message if any required var is missing.
 */

const REQUIRED_KEYS = [
  "NEXT_PUBLIC_SUPABASE_URL",
  "NEXT_PUBLIC_SUPABASE_ANON_KEY",
  "NEXT_PUBLIC_STRIPE_PUBLISHABLE_KEY",
] as const

function getEnv(key: string): string | undefined {
  if (typeof process === "undefined") return undefined
  return process.env[key]
}

function validateEnv(): {
  NEXT_PUBLIC_SUPABASE_URL: string
  NEXT_PUBLIC_SUPABASE_ANON_KEY: string
  NEXT_PUBLIC_STRIPE_PUBLISHABLE_KEY: string
} {
  const missing: string[] = []
  for (const key of REQUIRED_KEYS) {
    const value = getEnv(key)
    if (value === undefined || value.trim() === "") {
      missing.push(key)
    }
  }
  if (missing.length > 0) {
    const first = missing[0]
    throw new Error(
      `Missing required environment variable: ${first} - check your .env.local file`
    )
  }

  const url = getEnv(REQUIRED_KEYS[0])!.trim()
  const anonKey = getEnv(REQUIRED_KEYS[1])!.trim()
  const stripeKey = getEnv(REQUIRED_KEYS[2])!.trim()

  if (!url.startsWith("https://") || !url.includes(".supabase.co")) {
    throw new Error(
      "NEXT_PUBLIC_SUPABASE_URL must be the Supabase API URL (e.g. https://YOUR_REF.supabase.co). Use Project Settings → API → Project URL in the dashboard, not the Postgres connection string."
    )
  }

  if (typeof process !== "undefined" && process.env.NODE_ENV === "development") {
    // eslint-disable-next-line no-console
    console.log(
      "[Brasena] Env loaded: NEXT_PUBLIC_SUPABASE_URL, NEXT_PUBLIC_SUPABASE_ANON_KEY, NEXT_PUBLIC_STRIPE_PUBLISHABLE_KEY"
    )
  }

  return {
    NEXT_PUBLIC_SUPABASE_URL: url,
    NEXT_PUBLIC_SUPABASE_ANON_KEY: anonKey,
    NEXT_PUBLIC_STRIPE_PUBLISHABLE_KEY: stripeKey,
  }
}

export const env = validateEnv()
